import React from "react";
import { NavBar } from "../../../";

export default {
  title: "Components/NavBar",
  component: NavBar,
};

// Definição do tipo para os parâmetros do NavBar
interface NavBarLink {
  toId: string;
  text: string;
}

interface NavBarProps {
  links: NavBarLink[];
}

const navBarLinks: NavBarLink[] = [
  { toId: "section1", text: "Section 1" },
  { toId: "section2", text: "Section 2" },
  { toId: "section3", text: "Section 3" },
];

const Template = (args: NavBarProps) => (
  <NavBar.Root>
    <NavBar.Logo altText="Logo do site" />
    <NavBar.Content>
      {args.links.map((link) => (
        <NavBar.Link key={link.toId} toId={link.toId}>
          {link.text}
        </NavBar.Link>
      ))}
    </NavBar.Content>
  </NavBar.Root>
);
